@use 'base/variables' as v;
@use 'base/mixins' as m;

.formulario {
    margin-top: 3rem;
    .campo {
        display: flex;
        align-items: center;
        margin-bottom: 2rem;

        label {
            flex: 0 0 10rem;
            font-size: 1.7rem;
        }

        input, select {
            flex: 1;
            padding: 1rem;
            border: none;
            background-color: v.$grisCeleste;
            border: solid 0.2rem lighten(v.$gris, 46%);
            border-radius: .5rem;
        }

        select{
            cursor: pointer;
            appearance: none;
            background-color: v.$grisCeleste;
            color: v.$gris;
            &::-ms-expand{
                display: none;
            }

        }
    }
    option {
        cursor: pointer;
        &:hover{
            background-color: darken(v.$grisCeleste, 15%);
        }
    }
    
    select option:checked{
            background-color: lighten(v.$naranja, 20%);
    }
}