@use 'base/mixins' as m;
@use 'base/variables' as v;

.dashboard {
    background-color: v.$grisClaro;


    @include m.tablet {
        display: flex;
        height: 100vh;
    }
    .uptask {
        margin-bottom: 0;
        @include m.textoDegradado(v.$blanco, v.$naranja)
    }

    .principal {
        height: 100vh;
        flex: 1;
        overflow: scroll;

        .boton {
            @include m.boton(v.$naranja, v.$blanco);
            margin-top: 5rem;
            margin-bottom: 5rem;

            @include m.tablet {
                    width: 22rem;
            };
        }
    }

    .contenido {
        padding: 2rem;
        .nombre-pagina{
            color: v.$grisOscuro;
            font-size: 3.2rem;
            text-align: center;
            margin: 3rem 0;
        }
    }

    .enlace{
        color: v.$naranja;
        font-size: 1.8rem;
        font-weight: v.$bold;

        &:hover{
            color: lighten(v.$morado, 10%);
        }

        &.derecha{
            display: flex;
            justify-content: flex-end;
            margin-top: 4rem;
        }
    }

    .accionesProyecto {
        display: block;
        margin-top: 5rem;
        @include m.tablet {
            margin-top: 10rem;
            display: flex;
            justify-content: space-evenly;
        };
        .editarProyecto{
            @include m.boton(v.$verdeLight, v.$blanco);
            margin-bottom: 1rem;
            @include m.tablet {
                margin-bottom: 0;
                width: 20rem; 
                font-size: 1.4rem;
                align-content: center;
            };
            @include m.desktop {
                font-size: 1.6rem;
                width: 25rem; 
            };
        }
    
        .eliminarProyecto{
            @include m.boton(v.$rojo, v.$blanco);
    
            @include m.tablet {
                width: 20rem; 
                font-size: 1.4rem;
                align-content: center;
            };
            
            @include m.desktop {
                font-size: 1.6rem;
                width: 25rem; 
            };
        }
    }
}