@use 'base/variables' as v;
@use 'base/mixins' as m;

.modal{
    position: absolute;
    background-color: rgb(0 0 0 / .9);
    inset: 0;

    .formulario {
        legend {
            font-size: 2rem;
            display: block;
            text-align: center;
            font-weight: v.$bold;
            margin-bottom: 3rem;
            text-transform: uppercase;
            color: v.$cyan;
            
        }
        label {
            flex: 0 0 6rem;
            font-size: 1.4rem;
            color: v.$gris;

            @include m.tablet {
                flex: 0 0 7rem;
                font-size: 1.6rem;
            };
        }
        width: 95%;
        @include m.tablet {
            max-width: 60rem;            
        };

        .campo {
            font-size: 1.4rem;
            margin-bottom: 0rem;
            margin-top: 1rem;

            @include m.tablet {
                margin-top: 2rem
            };
        }

        .alerta {
            font-size: 1.2rem;

            @include m.tablet {
                font-size: 1.4rem;
            };
        }
        
        background-color: v.$blanco;
        padding: 5rem;
        border: solid 0.7rem darken(v.$grisCeleste, 10%);
        border-radius: 1.5rem;
        margin: 0 auto;
        transform: translateY(-100rem);
        transition: all 0.3s ease;

        &.animar{
            position: relative;
            top: 50%;
            transform: translateY(-55%);
            opacity: 1;
        }


        &.cerrar{
            transform: translateY(-180%);
            opacity: 0;
        }

    }

    .nueva-tarea {
        text-align: center
    }

    .opciones{
        margin-top: 3rem;
        .submit-nueva-tarea, .cerrar-modal{
            @include m.boton(v.$morado, v.$blanco);
            min-width: 22rem;
            margin: .5rem 0;
            padding: 1rem 4rem;
            font-size: 1.2rem;

            @include m.tablet {
                font-size: 1.6rem;
                padding: 1.5rem 4rem;
            };
        }

        @include m.tablet {
            display: flex;
            justify-content: space-between;
        };


    }


}