@use 'base/variables' as v;
@use 'base/mixins' as m;

.no-proyectos{
 text-align: center;
}

.listado-proyectos {
    list-style: none;
    padding-left: 0;
    
    @include m.grid(1, 2rem);
    @include m.tablet {
        @include m.grid(2, 2rem);
    };
    @include m.desktop {
        @include m.grid(3, 2rem);
    };
}
.proyecto {
    font-weight: v.$bold;
    font-size: 2rem;

    a {
        color: v.$blanco;
        min-height: 15rem;
        display: flex;
        align-items: center;
        justify-content: center;
        color: v.$grisCeleste;
        background-color: v.$rosa;
        padding: 1rem;
        border-radius: 0.7rem;
        text-align: center;
        transition: transform 0.3s ease;

        &:hover {
            cursor: pointer;
            transform: scale(1.1);
        }
    }

}