@use 'base/variables' as v;
@use 'base/mixins' as m;

.filtros {

    padding: 2rem;
    .filtros-inputs {
        @include m.tablet {
            display: flex;
            align-items:center;
            justify-content: center;
            gap: 1.5rem;
        }
    }
    h2 {
        text-align: center;
        color: v.$grisOscuro;
        font-size: 1.6rem;
        margin-bottom: .5rem;
        @include m.tablet {
            margin: 0;
        }
        @include m.desktop {
            font-size: 1.8rem;
        }
    }

    .campo {
        margin-bottom: 1rem;
        font-size: 1.2rem;
        display: flex;
        justify-content: space-between;
        align-items: center;
        gap: 1rem;
        @include m.tablet {
            margin-bottom: 0;
        }
        @include m.desktop {
            font-size: 1.4rem;
        }
    }

    input[type="radio"] {
        accent-color: v.$cyan;
        width: 1.6rem;
        height: 1.6rem;
    }

}