@use 'base/variables' as v;
@use 'base/mixins' as m;

.contenedor-nueva-tarea {
    display: flex;
    justify-content: center;
    .agregar-tarea{
        @include m.boton(v.$cyan, v.$blanco);
        margin-bottom: 5rem;
        border-radius: .5rem;
    }
}

.listado-tareas {
    align-items: center;
    list-style: none;
    padding-left: 0;
    
    .no-tarea {
        text-align: center;
        font-size: 2rem;
    }

    .contenedorPrincipal {
        padding: 1rem 0;
        border-bottom: 2px solid darken(v.$grisClaro, 5%);
        display: flex;

        .estadoTarea{
            min-width: 8.4rem;
            min-height: 4rem;
            margin: 0;
            font-size: 1rem;
            background-color: darken(v.$grisCeleste, 2%);
            font-weight: 600;
            border-radius: .5rem 0 0 .5rem;
            text-transform: uppercase;
            text-align: center;
            align-content: center;

            @include m.tablet {
                min-width: 10rem;
                font-size: 1.2rem;
            };

            @include m.desktop {
                min-width: 12rem;
                font-size: 1.4rem;
            };
        }
    
        .pendiente{
            color: v.$gris;
        }
        
        .enProceso{
            color: v.$naranja;
        }
    
        .completada{
            color: v.$verdeLight;
        }
        .tarea {
            display: flex;
            background-color: darken(v.$grisClaro, 1.5%);
            margin: 0;
            border-radius: 0 .5rem  .5rem 0;
            justify-content: space-between;
            width: 100%;
    
            .info {
                margin: 0;
                text-align: start;
                font-size: 0.9rem; 
                padding-left: 1rem;
                align-content: center;

                @include m.tablet {
                    font-size: 1.1rem;
                };

                @include m.desktop {
                    font-size: 1.2rem;
                };
            }

            .opciones {
                display: flex;
                gap: .3rem;
                margin-right: .3rem;

                .actualizar-tarea {
                    @include m.boton(v.$naranja, v.$blanco);
                    min-width: 5.9rem;
                    padding: 0.5rem;
                    margin: auto;
                    font-size: 0.9rem;
                    letter-spacing: normal;
                    border-radius: .5rem 0 0 .5rem;
    
                    @include m.tablet{
                        min-width: 8rem;
                        padding: .7rem;
                        font-size: 1.1rem;
                    }

                    @include m.desktop {
                        min-width: 9rem;
                        padding: .8rem;
                        font-size: 1.2rem;
                    };
                }
                
                .eliminar-tarea {
                    @include m.boton(v.$rosa, v.$blanco);
                    min-width: 5.9rem;
                    padding: 0.5rem;
                    margin: auto;
                    font-size: 0.9rem;
                    border-radius: .8rem;
                    letter-spacing: normal;
                    border-radius: 0 .5rem  .5rem 0;
    
                    @include m.tablet{
                        min-width: 8rem;
                        padding: .7rem;
                        font-size: 1.1rem;
                    }

                    @include m.desktop {
                        min-width: 9rem;
                        padding: .8rem;
                        font-size: 1.2rem;
                    };
                    
                }
            }
    
        }
    }

}