html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
  min-height: 100%;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

.contenedor-sm {
  max-width: 35rem;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .contenedor-sm {
    max-width: 50rem;
  }
}
@media (min-width: 1025px) {
  .contenedor-sm {
    max-width: 90rem;
  }
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

h1, h2, h3 {
  font-family: "Inter", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
}

h1 {
  font-size: 6rem;
}

h2 {
  font-size: 5rem;
}

h3 {
  font-size: 4rem;
}

p {
  font-size: 2rem;
  color: #6b7280;
  line-height: 1.8;
}

.tagline {
  font-family: "Inter", sans-serif;
  font-weight: 700;
  color: #000;
  font-size: 2rem;
  text-align: center;
  margin-bottom: 6rem;
  margin-top: 0;
}
@media (min-width: 768px) {
  .tagline {
    font-size: 3rem;
  }
}

.remover {
  display: none;
}

.descripcion-pagina {
  text-align: center;
  margin: 0;
  font-size: 2.2rem;
  color: #6b7280;
  font-weight: 400;
}
.formulario {
  margin-top: 3rem;
}
.formulario .campo {
  display: flex;
  align-items: center;
  margin-bottom: 2rem;
}
.formulario .campo label {
  flex: 0 0 10rem;
  font-size: 1.7rem;
}
.formulario .campo input, .formulario .campo select {
  flex: 1;
  padding: 1rem;
  border: none;
  background-color: #E8F0FE;
  border: solid 0.2rem #e9eaed;
  border-radius: 0.5rem;
}
.formulario .campo select {
  cursor: pointer;
  appearance: none;
  background-color: #E8F0FE;
  color: #6b7280;
}
.formulario .campo select::-ms-expand {
  display: none;
}
.formulario option {
  cursor: pointer;
}
.formulario option:hover {
  background-color: #9fc0fb;
}
.formulario select option:checked {
  background-color: #f9c56d;
}

.acciones {
  margin-top: 5rem;
}
.acciones a {
  display: block;
  text-align: center;
  margin-bottom: 1.5rem;
  color: #000;
  font-weight: 400;
}
.acciones a:last-of-type {
  margin-bottom: 0;
}

.alerta {
  display: block;
  width: 100%;
  padding: 1rem 1rem 1rem 2rem;
  margin: 1.5rem 0;
  font-size: 1.4rem;
  font-weight: 400;
  text-align: center;
}
.alerta:last-of-type {
  margin-bottom: 3rem;
}
.alerta.error {
  background-color: #f9dada;
  color: rgb(222, 51, 51);
  border-left: solid 0.6rem rgb(222, 51, 51);
}
.alerta.exito {
  background-color: #ccefcc;
  color: rgb(111, 155, 111);
  border-left: solid 0.6rem rgb(51, 163, 51);
}

.login {
  margin-top: 5rem;
}
.login .uptask {
  margin-bottom: 1rem;
  background: linear-gradient(to right, #0ab9e3 40%, #F59E0B 60%);
  color: transparent;
  background-clip: text;
  text-align: center;
  text-transform: uppercase;
  font-size: 6rem;
}
@media (min-width: 768px) {
  .login .uptask {
    font-size: 10rem;
  }
  .login .uptask.resize {
    width: 26rem;
    text-align: center;
    font-size: 5rem;
  }
}
.login input[type=submit] {
  background-color: #0891B2;
  padding: 1.5rem 3rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 400;
  border: none;
  border-radius: 0.5rem;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  letter-spacing: 0.1rem;
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .login input[type=submit] {
    width: auto;
  }
}
.login input[type=submit]:hover {
  background-color: #066981;
  cursor: pointer;
}
@media (min-width: 768px) {
  .login input[type=submit] {
    margin: 5rem auto 0;
  }
}
.login .acciones a {
  color: #0891B2;
}

