.sidebar {
  opacity: 0;
  background-color: #0891B2;
  overflow: hidden;
  height: 0;
  transition: opacity 0.5s ease;
}
.sidebar.mostrar {
  padding: 2rem;
  opacity: 0.98;
  height: 100%;
  position: absolute;
  inset: 0;
}
@media (min-width: 768px) {
  .sidebar.mostrar {
    display: none;
  }
}
.sidebar.ocultar {
  opacity: 0;
}
@media (min-width: 768px) {
  .sidebar {
    opacity: 1;
    height: 100%;
    padding: 2rem;
  }
}
.sidebar .logoHome {
  margin: 0;
  padding: 0;
  cursor: pointer;
}
.sidebar .contenedor-sidebar {
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 100%;
}
.sidebar .contenedor-sidebar .uptask {
  font-size: 5rem;
}
.sidebar .contenedor-sidebar .cerrar-menu {
  display: flex;
