@use 'base/variables' as v;
@use 'base/mixins' as m;

.barra-mobile {
    background-color: v.$cyan;
    padding: 2rem;
    display: flex;
    justify-content: space-around;
    align-items: center;
    @include m.tablet {
        display: none;
    }

    .uptask {
        font-size: 5rem;
    }
    .menu {
        width: 5rem;
        img {
            display: flex;
            margin: 0 auto;
            color: v.$blanco;
            width: .8rem;
            &:hover {
                cursor: pointer;
            }
        }
    }
}

.barra {

    display: none;
    background-color: v.$blanco;
    padding: 1rem 2rem;
    align-items: center;
    box-shadow: 0.1rem 1.2rem 0.9rem -1.3rem lighten(v.$negro, 70%);
    
    @include m.tablet {
        display: flex;
        justify-content: space-between;
        
    };

    p{
        margin: 1rem 0;
        text-align: center;
    }
    span {
        color: v.$naranja;
        font-weight: v.$bold;
        font-size: 2.2rem;
        
    }
    
    .cerrar-session {
        @include m.boton(v.$rosa, v.$blanco);
        font-size: 1.8rem;
        padding: 1.2rem;
    }

    p, a{
        @include m.tablet {
            margin: 0;
            padding: 2rem 1rem;
        };
    }


}