@use 'variables' as v;
@use 'base/mixins' as m;

h1, h2, h3 {
    font-family: v.$fuente_headings;
    margin: 0 0 calc(v.$separacion / 2) 0 ;
    font-weight: v.$black;
}
h1 {
    font-size: 6rem;
}
h2 {
    font-size: 5rem;
}
h3 {
    font-size: 4rem;
}
p {
    font-size: 2rem;
    color: v.$gris;
    line-height: 1.8;
}

.tagline {
    font-family: v.$fuente_headings;
    font-weight: v.$bold;
    color: v.$negro;
    font-size: 2rem;
    text-align: center;
    margin-bottom: 6rem;
    margin-top: 0;
    
    @include m.tablet {
        font-size: 3rem;
        
    };
}

.remover{
    display: none;
}

.descripcion-pagina{
    text-align: center;
    margin: 0;
    font-size: 2.2rem;
    color: v.$gris;
    font-weight: v.$regular;

    @include m.tablet {
        
    };

}