@use 'base/mixins' as m;
@use 'base/variables' as v;

.sidebar {
    opacity: 0;
    background-color: v.$cyan;
    overflow: hidden;
    height: 0;
    transition: opacity 0.5s ease;

    &.mostrar {
        padding: 2rem;
        opacity: .98;
        height: 100%;
        position: absolute;
        inset: 0;
        

        @include m.tablet {
            display: none
        };
    }

    &.ocultar {
        opacity: 0;
    }

    @include m.tablet {
        opacity: 1;
        height: 100%;
        padding: 2rem;
    }

    .logoHome{
        margin: 0;
        padding: 0;
        cursor: pointer;
    }

    
    .contenedor-sidebar {
        display: flex;
        justify-content: space-around;
        align-items: center;
        width: 100%;
        
        .uptask {
            font-size: 5rem;
        }
        .cerrar-menu {
            display: flex;
            justify-content: center;
            img {
                width: 5rem;
                color: v.$blanco;
                &:hover {
                    cursor: pointer;
                }
            }
            @include m.tablet {
                display: none;
            }
        }
    }

    .sidebar-nav{
        margin-top: 5rem;
        a {
            color: v.$blanco;
            display: block;
            font-size: 2.4rem;
            font-weight: v.$regular;
            border-radius: .5rem;
            padding: 1.5rem;
            text-align: center;
            transition: all 0.4s ease;

            @include m.tablet {
                text-align: left;
                font-size: 2rem;
            }

            &:hover {
                background-color: v.$naranja;
                padding: 1.28rem;
                font-size: 2.4rem;
            }
        }
        .activo {
            color: v.$naranja;
            font-weight: v.$bold;
            background-color: v.$grisClaro;

            &:hover {
                background-color: v.$blanco;
                padding: 1.28rem;
                font-size: 2.4rem;
            }
        }

    }

    .cerrar-sesion-mobile {
        margin-top: 10rem;
        p {
            text-align: center;
            color: v.$blanco;
            font-size: 2.4rem;
        }
        span{
            color: lighten(v.$naranja, 18%);
            letter-spacing: 0.1rem;
            font-weight: v.$bold;

        }
        .cerrar-session {
            @include m.boton(v.$rosa, v.$blanco);
            font-size: 1.8rem;
            padding: 1.2rem;
        }

        

        @include m.tablet {
            display: none;
        }
    }

}