@use 'base/variables' as v;

.alerta {
    display: block;
    width: 100%;
    padding: 1rem 1rem 1rem 2rem;
    margin: 1.5rem 0;
    font-size: 1.4rem;
    font-weight: v.$regular;
    text-align: center;

    &:last-of-type {
        margin-bottom: 3rem;
    }

    &.error {
        background-color: lighten(v.$rojo, 38%);
        color: v.$rojo;
        border-left: solid .6rem v.$rojo;
    }

    &.exito {
        background-color: lighten(v.$verde, 45%);
        color: v.$verdeLight;
        border-left: solid .6rem v.$verde;
    }
}