@use 'base/variables' as v;
@use 'base/mixins' as m;

@media(prefers-color-scheme: dark) {
    body{
        background-color: darken(v.$grisOscuro, 20%);
    }
    .tagline,
    .formulario label,
    .login .acciones a,
    .crear .acciones a,
    .olvide .acciones a {
        color: v.$blanco;
    }

    .descripcion-pagina,
    .formulario .campo input,
    .nombre-pagina {
        color: v.$grisClaro;
    }

    .formulario .campo input {
        background-color: lighten( v.$grisOscuro, 5% );
        border-color: lighten( v.$grisOscuro, 15% );
        &::placeholder {
            color: darken(v.$grisClaro, 30%);
        }
    }

    .alerta.error {
        background-color: darken( v.$grisOscuro, 15% );
        color: lighten( v.$rojo, 15% );
        font-size: 1.7rem;
        font-weight: v.$bold;
        border-color: lighten( v.$rojo, 15% );
    }

    .sidebar {

        @include m.tablet {
            background-color: darken(v.$grisOscuro, 10%);
        }
        .sidebar-nav a {
            &.activo, &.activo:hover,
            &:hover {
                background-color: v.$grisOscuro;
            }
        }

        &.mostrar {
            background-color: darken(v.$grisOscuro, 20%);
            .cerrar-session {
                @include m.boton(v.$gris, v.$blanco);
            }
        }
    }

    .dashboard {
        background-color: darken(v.$grisOscuro, 20%);

        .principal .contenido{

            .nombre-pagina{
                color: v.$blanco;
            }
            .descripcion-pagina{
                color: darken(v.$grisClaro, 20%);
            }
        }
    }

    .filtros {
        h2,
        label {
            color: v.$blanco;
        }
    }
    
    .listado-proyectos .proyecto a{
        background-color: darken(v.$grisOscuro, 5%);
        &:hover {
            background-color: lighten(v.$grisOscuro, 15%);
        }
    }
    
    .listado-tareas .no-tarea {
        color: v.$blanco;
    }

    .listado-tareas .contenedorPrincipal {
        border-bottom-color: darken(v.$grisOscuro, 5%);
        .tarea {
            background-color: darken(v.$grisOscuro, 15%);
            border: none;
            p {
                color: v.$grisClaro;
            }
        }

        .estadoTarea{
            background-color: darken(v.$grisOscuro, 10%);

            &.pendiente{
                color: lighten(v.$grisOscuro, 45%);
            }
        }
    }

    .barra-mobile,
    .barra {
        background-color: darken(v.$grisOscuro, 15%);
    }
    .barra {
        p {
            color: v.$grisClaro;
            span {
                color: v.$naranja;
            }
        }
        .cerrar-session {
            @include m.boton(v.$gris, v.$blanco);
        }
    }

    .modal .formulario {
        border-color: v.$gris;
        background-color: darken(v.$grisOscuro, 15%);
        input[type="text"] {
            background-color: lighten( v.$grisOscuro, 10%);
            border: none;
            color: v.$blanco;
            &::placeholder {
                color: darken(v.$grisClaro, 20%);
                border: none;
            }
        }
        
        select{
            color: v.$blanco;
            background-color: lighten( v.$grisOscuro, 10%);

            option {
                &:hover{
                    background-color: darken(v.$gris, 15%);
                }
            }
        }

        select option:checked{
            background-color: lighten(v.$gris, 20%);
    }
        
        legend, label {
            color: v.$grisClaro;
        }
    }
}